import * as React from 'react';
import { Link, graphql } from 'gatsby';
import fontawesome from '@fortawesome/fontawesome';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearch } from '@fortawesome/free-solid-svg-icons';

import Layout from '../components/layout';
import Seo from '../components/seo';
import { PRODUCT_FILTERS } from '../constants/products-constants';

fontawesome.library.add(faSearch);

const SearchBox = ({ searchInput, onSearchInput, onSearch }) => {
  return (
    <form className="input-group rounded" onSubmit={onSearch}>
      <input type="search" className="form-control rounded" placeholder="Search" value={searchInput} aria-label="Search" aria-describedby="search-addon" onChange={onSearchInput} />
      <span className="input-group-text border-0" id="search-addon">
        <FontAwesomeIcon icon="search" />
      </span>
    </form>
  );
}

export default class ProductsPage extends React.Component {
  constructor({ data, location, activeSearchFilter = '' }) {
    super()
    this.state = {
      activeFilter: Object.keys(PRODUCT_FILTERS)[0],
      activeSearchFilter,
      searchInput: activeSearchFilter,
      data,
      location,
      products: data.allMarkdownRemark.nodes
    };
  }

  filterProducts = () => {
    this.setState({
      products: this.state.data.allMarkdownRemark.nodes
        .filter(product => {
          const filterTags = (PRODUCT_FILTERS[this.state.activeFilter].tags || []).filter(Boolean);
          const productTags = (product.frontmatter.tags || []).filter(Boolean);
          if (filterTags.length > 0) {
            return filterTags.some(filterTag => productTags.includes(filterTag));
          } else {
            return true;
          }
        })
        .filter(product => {
          const activeSearchFilter = this.state.activeSearchFilter.toLowerCase();
          const productTitle = product.frontmatter.title.toLowerCase();
          const productDescription = product.frontmatter.description.toLowerCase();
          return productTitle.includes(activeSearchFilter) || productDescription.includes(activeSearchFilter);
        })
      });
  }

  onSetActiveFilter = activeFilter => {
    this.setState({ activeFilter });
    this.filterProducts();
  }

  onSearch = event => {
    event.preventDefault();
    this.setState({ activeSearchFilter: this.state.searchInput });
    this.filterProducts();
  }

  onSearchInput = event => {
    event.preventDefault();
    this.setState({ searchInput: event.target.value });
  }

  render() {
    const siteTitle = this.state.data.site.siteMetadata?.title || 'Title';
    const pageTitle = 'Products';

    return (
      <div>
        <Seo title={pageTitle} />
        <ProductsIndex
          products={this.state.products}
          searchInput={this.state.searchInput}
          activeSearchFilter={this.state.activeSearchFilter}
          activeFilter={this.state.activeFilter}
          onSearch={this.onSearch}
          onSearchInput={this.onSearchInput}
          onSetActiveFilter={this.onSetActiveFilter}
        />
      </div>
    );
  }
}

const ProductsIndex = ({ products, searchInput, activeSearchFilter, activeFilter, onSearch, onSearchInput, onSetActiveFilter }) => {
  return (
    <div className="container mt-5 pb-5">
      <div className="row">
        <aside className="col-12 col-md-4 col-lg-3">
          <ul className="list-group mb-3">
            <li key="fast-product-selection-table" className="list-group-item list-group-item-action btn">Fast Product Selection Table</li>
            <li key="spice-model-library-download" className="list-group-item list-group-item-action btn">SPICE Model Library Download</li>
          </ul>

          <div className="mb-3">
            <SearchBox searchInput={searchInput} onSearchInput={onSearchInput} onSearch={onSearch} />
          </div>

          <ul className="list-group mb-3">
            {Object.entries(PRODUCT_FILTERS).map(([filterCode, filter]) => {
              const isActiveFilter = filterCode === activeFilter;

              return (
                <li
                  key={filterCode}
                  onClick={() => onSetActiveFilter(filterCode)}
                  className={`list-group-item list-group-item-action btn ${ isActiveFilter ? 'active' : '' }`}
                  aria-current={isActiveFilter ? 'true' : 'false'}
                >
                  {filter.name}
                </li>
              );
            })}
          </ul>

          <ul className="list-group mb-3">
            <li key="lead-free-rohs-fact-sheet" className="list-group-item list-group-item-action btn">Lead-free / RoHS Fact Sheet</li>
            <li key="lead-free-rohs-compliant-parts-list" className="list-group-item list-group-item-action btn">Lead-free / RoHS Compliant Parts List</li>
          </ul>
        </aside>

        <div className="col">
          <div className="container">
            { !!activeSearchFilter ? <div class="row"><div class="col h4 mb-3">Searching for "{activeSearchFilter}"</div></div> : <div></div>}
            <div className="row row-cols-1 row-cols-sm-2 row-cols-md-2 row-cols-lg-4 row-cols-xl-5 g-3">
              {products
                .map(product => {
                  const slug = product.fields.slug;
                  const title = product.frontmatter.title || slug;
                  const productImageUrl = product.frontmatter.productImageUrl;

                  return (
                    <div className="col" key={slug}>
                      <Link to={slug} itemProp="url" className="text-dark text-decoration-none">
                        <div className="card">
                          <img className="card-img-top img-fluid px-3" src={productImageUrl} alt={title} />
                          <div className="card-body">
                            <span className="card-title">{title}</span>
                          </div>
                        </div>
                      </Link>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      filter: {
        frontmatter: {
          templateKey: {
            eq: "product-post"
          }
        }
      },
      sort: {
        fields: [frontmatter___date],
        order: DESC
      })
    {
      nodes {
        excerpt(pruneLength: 110)
        fields {
          slug
        }
        frontmatter {
          title
          description
          productImageUrl
          tags
        }
      }
    }
  }`;
